<template>
    <div id="listing">
        <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('Ticket Report') }}</span>
                       
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"></dynamic-form>
          
         
        </div>
    </section>
   </div>
</template>
<script>
import DataServices from '../../services/DataService';
export default {
    components: {  },
    data() {
        return {
            statusList:[],
            isLoading:false,
            CompanyParentClientCustomListByCompanyIdFor:[],
            userList:[],
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [  
                    {
                            astype: "TextField",
                            label: this.$t('TicketNumber'),
                            name: "TicketNumber",
                            value: this.$route.query.TicketNumber,
                            placeholder: ""
                                                 
                        },                              
                        {
                            astype: "TextField",
                            label: this.$t('Subject'),
                            name: "Subject",
                            value: this.$route.query.subject,
                            placeholder: ""
                                                 
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Submitted By'),
                            name: "User",
                            value: this.$route.query.raisedById,
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Assigned To'),
                            name: "AssignedTo",
                            value: this.$route.query.assignedToId,
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Client'),
                            name: "Client",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false
                              
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Priority'),
                            name: "Priority",
                            value: this.$route.query.priorityId,
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false                              
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('AssignDepartment'),
                            name: "AssignDepartment",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false                              
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: '',
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Category'),
                            name: "TicketCategory",
                            value: '',
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('OpenDate'),
                            name: "OpenFromDate",
                            value: "",
                            placeholder: "From",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Channel'),
                            name: "Channel",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        }
                    ]
                },
            ],
            buttons: [
                {
                    type: "search",
                    class: "btn btn-success formbtn text-white",
                    text: "<i class='fa fa-search pr-2'></i>Search",                    
                },
                {
                    type: "ClearAll",
                    class: "btn btn-danger formbtn widthhalf btnCancel",
                    text: "<i class='fa fa-close pr-2'></i>Clear All"
                },
              
            ],
        }                
    },    
    created:function()
    {
        var vm=this;
        vm.GetCompanyParentClientCustomListByCompanyIdFor();
        vm.GetUserList();
        vm.GetStatusList();
        vm.GetPrioritybyCompanyId();
       

    },
    methods: {

        GetCompanyParentClientCustomListByCompanyIdFor:function(){
            var vm = this;
            DataServices.GetCompanyParentClientCustomListByCompanyIdForGroup().then(response => {
                vm.CompanyParentClientCustomListByCompanyIdFor= response.data;
           
            });
        },

        GetUserList:async function(){
            var vm=this;
            var params = `isActive=${1}&moduleName=${'TICKETING'}`;
            await  DataServices.GetUserList(params).then(async(response) => {
                 if (response.data.length > 0) {
                   vm.userList=response.data;
                   vm.userList.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[2].config.options.push({
                        value: item.userId,
                        name: item.userName
                    })
                 })
                 }
                 
            });
        },
        GetPrioritybyCompanyId:async function(){
            debugger;
            var vm=this;
            await  DataServices.GetPrioritybyCompanyId().then(async(response) => {
                 if (response.data.length > 0) {
                   vm.priorityList=response.data;
                   vm.priorityList=vm.priorityList.map(item=>{ return {value:item.prioritY_ID,name: item.prioritY_NAME}})
                   $.each(vm.FormSchema, function (index, item) {
                $.each(item.Data, function (index, element) {
                    if (element.name == 'Priority') {
                        element.value = '';
                        element.config.options = vm.priorityList;
                    }
                });
            });
                 }
            });
         },
        GetStatusList:function()
        {
            var vm = this;
          var url = `langCode=&statusType=CRM&hasglobal=false`
          DataServices.GetStatusList(url).then(response => {
             vm.statusList= response.data;
     });


        },

        GetAllDepartment:async  function()
        {  var vm=this;
            await  DataServices.GetAllDepartment().then(async(response) => {
                 if (response.data.length > 0) {
                   vm.priorityList=response.data;
                   vm.priorityList.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[5].config.options.push({
                        value: item.prioritY_ID,
                        name: item.prioritY_NAME
                    })
                })
                 }
            });
         },
    //     GetCompanyClientsByCompanyId:function(){
    //         var vm = this;
    //         DataServices.GetCompanyClientsByCompanyId().then(response => {
    //         vm.companyClientsByCompanyId= response.data;
              
    //  });
    //     },
        onSearch: function () {
            var vm = this;
            var reportModel = {
                reportName: "crmdetailreport",
                moduleName: "crm",
                dateFrom: vm.dtFrom,
                dateTo: vm.dtTo,
                StatusIds:vm.StatusId.toString(),
                ClientIds:vm.ClientId.toString(),
                ownerIds:vm.OwnerIds.toString(),
                crmType:1,
                leadValueFrom:vm.leadValueFrom.toString(),
                leadValueTo:vm.leadValueTo.toString(),
              


            };
            var objectPostString = JSON.stringify(reportModel);
            DataService.ReportMain(objectPostString).then(response => {
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
            });
        },
        onClear: function () {
            this.dtFrom=null;
            this.dtTo=null;
            this.parameter=[];
            this.reportPath=null;
            this.StatusId=null;
            this.OwnerIds=null;
            this.ClientId=null;
            this.leadValueTo=null;
            this.leadValueFrom=null;
            
        },
        DependentDateObject: function () {
            this.dtTo = this.dtFrom;
            this.mindate=this.dtTo;
        }


    }
}
</script>