<template>
    <div class="container-fluid padding-t_10" id="reportstabsdata">
    <div class="left-menu-tab new_tab">
        <div class="col-md-12 p-0 ">
            <div class="theme-primary partition">
                <span class="p-name text-white">{{$t('Ticket')}} {{$t('Reports')}}</span>
            </div>
        </div>
        <div class="pt-2 mb-1 w-100 d-none d-lg-block">
            <div class="input-group customsearchbox">
                <input type="text" maxlength="50" :placeholder="$t('SearchbyFieldName')"  class="form-control searchcustomfilter border-right-0" autocomplete="off">
                <div class="input-group-append"><span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search"></i></span></div>
            </div>
        </div>
        <div class="mt-2" id="divscrollsetuptabs" style="max-height:700px">
            <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix lefttabflexible" id="upper" role="tablist">
                
                    <li v-if="UserLicenseModules.find(x=>x.controllerName.toUpperCase()=='TICKETREPORT' && x.actionName.toUpperCase()=='TICKETDETAIL')">
                        <a class="nav-link left-menu" data-toggle="tab" :class="{'active':activeKey==1}" data-bs-toggle="#TicketReport"  href="javascript:void(0)" id="aTicketReport" data-text="TicketDetail" data-ordername="TicketSummary" privilege="TicketDetail">
                            <i class="fa fa-ticket" aria-hidden="true"></i>
                          {{$t('Ticket')}} {{$t('Summary')}} {{$t('Report')}}
                        </a>
                    </li>
                
             
                    <li v-if="UserLicenseModules.find(x=>x.controllerName.toUpperCase()=='TICKETREPORT' && x.actionName.toUpperCase()=='TICKETINGPRIVILEGEREPORT')">
                        <a class="nav-link left-menu" data-toggle="tab" :class="{'active':activeKey==2}" data-bs-toggle="#TicketingPrivilegeReport" href="javascript:void(0)" id="aTicketingPrivilegeReport" data-text="TicketingPrivilegeReport" data-ordername="TicketingPrivilegeReport" privilege="TicketingPrivilegeReport">
                            <i class="fa fa-file-text" aria-hidden="true"></i>
                         {{$t('TicketingPrivilege')}} {{$t('Report')}}
                        </a>
                    </li>
        
              
          <li v-if="UserLicenseModules.find(x=>x.controllerName.toUpperCase()=='TICKETREPORT' && x.actionName.toUpperCase()=='TICKETDETAILREPORT')">
                        <a class="nav-link left-menu" data-toggle="tab" :class="{'active':activeKey==3}" data-bs-toggle="#TicketDetailReport" href="javascript:void(0)" id="aTicketDetailReport" data-text="TicketDetailReport" data-ordername="TicketDetailReport" privilege="TicketDetailReport">
                            <i class="fa fa-file-pdf-o" aria-hidden="true"></i>
                           {{$t('Ticket')}} {{$t('Detail')}} {{$t('Report')}}
                        </a>
                    </li>
              
            </ul>
        </div>
    </div>
    <div class="right-content">
        <div class="col-md-12 p-0">
            <div class="theme-primary partition p-actions-expand" style="overflow: visible;">
                <span class="p-name text-white" id="spnreporttext"></span>
                <span class="p-actions float-right">
                    <a href="javascript:;" id="ancshowmorefilter" class="p-action-btn text-white" style="display:none;" :title="$t('Show') +' '+$t('More')+' '+ $t('Filter')"><i class="fa fa-filter" aria-hidden="true"></i><br>{{$t('Show')}} {{$t('More')}} {{$t('Filter')}}</a>
                </span>
              
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                </span>
            </div>
        </div>
        <div class="col-md-12 p-0">
            <div class="tab-content" id="myTabContent">
                <div class="expand-list">
                    <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                        <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                    </a>
                </div>
                <div id="TicketReport" class="tab-pane" :class="{'active show':activeKey==1}" v-if="activeKey==1">
                 <TicketReport></TicketReport>
                </div>
                <div id="TicketingPrivilegeReport" class="tab-pane "></div>
                <div id="TicketDetailReport" class="tab-pane "></div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import $ from "jquery";
import TicketReport from "./TicketReport.vue";
import DataService from "../../services/DataService";
export default {
    components: { TicketReport },
   data() {
        return {
            UserLicenseModules: null,
            ConfgurationValueByKey: null,
            activeKey: 1,
            isLoading:false,
        };
    },
    created: function () {
        this.GetUserLicenseModulesReport();
    },
    mounted:function ()
    {           
        $(".searchcustomfilter").keyup(function (event) {
        event.stopImmediatePropagation();
        var val = $(this).val();
        $("ul#upper").find("li").each(function () {
            val = String(val).toLowerCase();
            var text = String($(this).find("a").text()).toLowerCase();
            if (text.includes(val)) {
                $(this).closest("li").show();
            }
            else {
                $(this).closest("li").hide();
            }
        });
    });
        $("#reportstabsdata").on("click", "a.nav-link", function (e) {
            e.stopImmediatePropagation();
            $("#spnreporttext").html($(this).text());
        //  vm.loadCRMDetailReport(actionname, nameprivilege, $(this));
        });
    },
    methods: {
        GetPrivilege: function (param) {
           (async()=>{
          await  DataService.CheckPrivilege(param)
                .then(function (response) {
            });
        })
        },
        GetUserLicenseModulesReport: function () {
            var vm = this;
            vm.isLoading=true;
            DataService.GetUserLicenseModulesReport().then(response => {
                vm.isLoading=false;
                vm.UserLicenseModules = response.data.list;
            });
        },
        GetConfgurationValueByKey: function () {
            var vm = this;
            DataService.GetConfgurationValueByKey().then(response => {
                vm.ConfgurationValueByKey = response.data.list;
            });
        },
        loadTicketReport: function (actionname, nameprivilege, ctrl) {
         
            $($(this).attr('href')).hide();
            var ticketype="";
            if (actionname == "TicketReport") {
                this.oadCRMDetail(ticketype, actionname);
            }
            else{
                if (this.GetPrivilege('controller=TicketDetail&action='+actionname)) {
                    if (actionname == "TicketReport") {
                        actionname = "TicketDetailReport";
                        ticketype = "l";
                    }
                    this.loadCRMDetail(ticketype, actionname);
                }
                else {
                    $("#DisplayReport").html('<h2>Sorry, You are not authorized to access this section</h2>');
                }
            }
            }
          
      

    }    
}

</script>